.select-list {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 320px;
  position: relative;
}
.select-list__header {
  font-family: SourceSansPro-SemiBold;
  padding-left: 15.5px;
  position: relative;
  height: 40px;
  border: 1px solid #e3e9ef;
  border-radius: 6px;
  line-height: 36px;
  color: #b8c1cc;
  transition: 0.3s;
}
.select-list__header:before {
  content: '';
  position: absolute;
  right: 15px;
  top: 45%;
  border: 7px solid transparent;
  border-top: 8px solid #8392a6;
}
.select-list__header:hover {
  color: rgba(59,72,88,0.9);
  border-color: rgba(59,72,88,0.9);
}
.select-list__list {
  display: none;
  position: absolute;
  border: 1px solid #e3e9ef;
  border-radius: 6px;
  height: 200px;
  overflow: scroll;
  top: 40px;
  width: 100%;
  background-color: #fff;
}
.select-list__list.open {
  display: block;
}
.select-list__list-item {
  font-family: SourceSansPro-SemiBold;
  padding-left: 15.5px;
  line-height: 36px;
  color: #8392a6;
  width: 100%;
}
.select-list__list-itemhover {
  background-color: #f9f9f9;
  color: #3b4858;
}
.switch-btn {
  height: 30px;
}
.switch-btn .checkbox {
  position: absolute;
  opacity: 0;
}
.switch-btn .checkbox:checked + .knobs > .on {
  border: 1px solid #1fb6ff;
  background-color: #1fb6ff;
  line-height: 25px;
  width: 50%;
  color: #fff;
  height: 100%;
}
.switch-btn .checkbox:checked + .knobs > .off {
  background-color: transparent;
  border: 1px solid #e0e6ed;
  color: #8392a6;
  line-height: 25px;
  width: 50%;
  height: 100%;
}
.switch-btn .checkbox:checked:hover + .knobs > .on {
  background-color: #08a5f1;
  border-color: #08a5f1;
}
.switch-btn .checkbox:checked:hover + .knobs > .off {
  background-color: transparent;
  color: rgba(59,72,88,0.9);
  border: 1px solid #8392a6;
}
.switch-btn .knobs {
  height: 30px;
  width: 139px;
  display: flex;
  position: relative;
}
.switch-btn .knobs:hover > .on {
  border-color: rgba(59,72,88,0.9);
  color: rgba(59,72,88,0.9);
}
.switch-btn .knobs:hover > .off {
  background-color: #08a5f1;
  border-color: #08a5f1;
  border: 1px solid #8392a6;
}
.switch-btn .knobs .on {
  border-bottom-left-radius: 6px;
  border-top-left-radius: 6px;
  border: 1px solid #e0e6ed;
  color: #8392a6;
  line-height: 25px;
  width: 50%;
  height: 100%;
}
.switch-btn .knobs .off {
  border-bottom-right-radius: 6px;
  border-top-right-radius: 6px;
  border: 1px solid #1fb6ff;
  background-color: #1fb6ff;
  line-height: 25px;
  width: 50%;
  color: #fff;
  height: 100%;
}
.checkbox-btn .checkbox {
  position: absolute;
  opacity: 0;
}
.checkbox-btn .checkbox:checked + .knob:before {
  opacity: 1;
}
.checkbox-btn .checkbox:checked + .knob:after {
  opacity: 0;
}
.checkbox-btn .knob {
  position: relative;
  width: 24px;
  height: 24px;
}
.checkbox-btn .knob:hover .checkbox-btn .knob:after {
  background-color: $color_biglRiff;
}
.checkbox-btn .knob:after {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
  transition: 0.3s;
  display: block;
  content: '';
  height: 24px;
  width: 24px;
  background-size: 24px 24px;
  min-width: 24px;
  min-height: 24px;
  mask-size: 24px 24px;
  mask-image: url("../img/icons/checkbox.svg");
  cursor: pointer;
  background-color: #1fb6ff;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .checkbox-btn .knob:after {
    background-color: transparent !important;
    mask-image: none !important;
    background-image: url("../img/icons/checkbox.svg");
  }
}
.checkbox-btn .knob:before {
  position: absolute;
  content: "";
  display: block;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  transition: 0.3s;
  display: block;
  content: '';
  height: 24px;
  width: 24px;
  background-size: 24px 24px;
  min-width: 24px;
  min-height: 24px;
  mask-size: 24px 24px;
  mask-image: url("../img/icons/checkbox-empty.svg");
  cursor: pointer;
  background-color: #e0e6ed;
}
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .checkbox-btn .knob:before {
    background-color: transparent !important;
    mask-image: none !important;
    background-image: url("../img/icons/checkbox-empty.svg");
  }
}
.tooltip__btn {
  width: 22px;
  height: 22px;
  background-color: rgba(131,146,166,0.5);
  position: relative;
}
.tooltip__btn:hover {
  background-color: #8392a6;
}
.tooltip__btn:hover:after {
  display: block;
}
.tooltip__btn:hover:before {
  display: block;
}
.tooltip__btn:after {
  display: none;
  position: absolute;
  content: attr(tooltip);
  padding: 10px 14px 10px 14px;
  font-size: 14px;
  text-align: center;
  color: rgba(59,72,88,0.9);
  border-radius: 6px;
  background-color: #fff;
  box-shadow: 0 2px 4px 0 rgba(59,72,88,0.4);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  z-index: 10;
  left: 50%;
  transform: translate(-50%, -60px);
  color: #000;
}
.tooltip__btn:before {
  display: none;
  z-index: 11;
  content: '▴';
  position: absolute;
  left: 50%;
  transform: translate(-50%, -43px) rotate(180deg);
  color: #fff;
  font-size: 53px;
  text-shadow: 0px -2px 3px rgba(59,72,88,0.4);
}
